.btn {
  text-align: center;
  cursor: pointer;
  color: #131313;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
}

.btn--standart {
  background-color: #f8d24e;
  border: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--standart:hover {
  background-color: #0a8817;
}

.btn--standart:focus {
  color: #fcfcfc;
  background-color: #0a8817;
}

.btn--transparent {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--transparent:hover {
  border: 2px solid #0a8817;
}

.btn--transparent:focus {
  color: #fcfcfc;
  border: 2px solid #0a8817;
}

